import {
  Typography,
  Card,
  CardMedia,
  CardContent,
  Button,
  CardActions
} from "@material-ui/core";
import React, { Component, Fragment } from "react";
import "./Output.css";
import coopet from "../../assets/coopet.png";

const isMobile = window.innerWidth <= 500;
const outputs: any[] = [
  {
    targetImage: null,
    outputName: "Portfolio",
    content: "このポートフォリオです。Reactで開発しました。",
    url: "https://ringoku.github.io"
  },
  {
    targetImage: coopet,
    outputName: "Coopet",
    content:
      "ペットでつながるSNSです。React+Firebaseで開発しました。CI/CDにCircle CIを導入し、PWA対応も行っています。",
    url: "https://coopet-51a0b.web.app"
  }
];

export class Output extends Component {
  goToPage = (url: string) => {
    window.location.href = url;
  };
  getOutputCard = (output: any) => {
    const { targetImage, outputName, content, url } = output;
    return (
      <Card
        className={isMobile ? "skill-detail-card-mobile" : "skill-detail-card"}
        onClick={() => this.goToPage(url)}
      >
        {targetImage && (
          <CardMedia
            image={targetImage}
            className="skill-image"
            component="img"
            title="Contemplative Reptile"
          />
        )}
        <CardContent>
          <Typography gutterBottom variant="h5" component="h2">
            {outputName}
          </Typography>
        </CardContent>
        <Typography variant="body2" color="textSecondary" component="p">
          {content}
        </Typography>
        <CardActions className="output-actions">
          <Button variant="contained" size="small" color="primary">
            GoTo
          </Button>
        </CardActions>
      </Card>
    );
  };
  render = () => {
    if (isMobile) {
      return (
        <Fragment>
          <Typography variant="h3">Works</Typography>
          {outputs.map(output => this.getOutputCard(output))}
        </Fragment>
      );
    } else {
      return (
        <div className="output-row">
          {outputs.map(output => this.getOutputCard(output))}
        </div>
      );
    }
  };
}

export default Output;
