import {
  Typography,
  Card,
  CardMedia,
  CardContent,
  Table,
  TableHead,
  TableRow,
  TableCell,
  TableBody
} from "@material-ui/core";
import React, { Component, Fragment } from "react";
import "./SkillDetail.css";
import angular from "../../assets/angular.svg";
import java from "../../assets/java.svg";

const isMobile = window.innerWidth <= 500;
const frontendRows = [
  {
    name: "HTML+CSS+ES2015",
    term: "3 year"
  },
  {
    name: "TypeScript",
    term: "3 year"
  },
  {
    name: "Angular(2～7)",
    term: "3 year"
  },
  {
    name: "React",
    term: "2month"
  },
  {
    name: "Vue",
    term: "a half year"
  },
  {
    name: "jQuery",
    term: "1 Year"
  }
];

const backendRows = [
  {
    name: "Java(5～8)",
    term: "3 year"
  },
  {
    name: "Spring Framework",
    term: "3 year"
  },
  {
    name: "Jersey",
    term: "3 year"
  },
  {
    name: "MyBatis",
    term: "2month"
  },
  {
    name: "Firebase",
    term: "2month"
  }
];

const infraRows = [
  {
    name: "apache",
    term: "2 year"
  },
  {
    name: "tomcat",
    term: "2 year"
  },
  {
    name: "JBoss EAP",
    term: "1 year"
  },
  {
    name: "RHEL",
    term: "2month"
  },
  {
    name: "Bash",
    term: "1month"
  }
];

export class SkillDetail extends Component {
  getSkillDetailCard = (
    targetImage: any,
    skillName: string,
    targetRows: any[]
  ) => {
    return (
      <Card
        className={isMobile ? "skill-detail-card-mobile" : "skill-detail-card"}
      >
        {targetImage && (
          <CardMedia
            image={targetImage}
            className="skill-image"
            component="img"
            title="Contemplative Reptile"
          />
        )}
        <CardContent>
          <Typography gutterBottom variant="h5" component="h2">
            {skillName}
          </Typography>
          <Table size="small">
            <TableHead>
              <TableRow>
                <TableCell>Name</TableCell>
                <TableCell align="right">Term</TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {targetRows.map(row => (
                <TableRow key={row.name}>
                  <TableCell component="th" scope="row">
                    {row.name}
                  </TableCell>
                  <TableCell align="right">{row.term}</TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </CardContent>
      </Card>
    );
  };
  render = () => {
    if (isMobile) {
      return (
        <Fragment>
          {this.getSkillDetailCard(angular, "FrontEnd Skill", frontendRows)}
          {this.getSkillDetailCard(java, "BackEnd Skill", backendRows)}
          {this.getSkillDetailCard(null, "Infrastructure Skill", infraRows)}
        </Fragment>
      );
    } else {
      return (
        <div className="skill-detail-row">
          {this.getSkillDetailCard(angular, "FrontEnd Skill", frontendRows)}
          {this.getSkillDetailCard(java, "BackEnd Skill", backendRows)}
          {this.getSkillDetailCard(null, "Infrastructure Skill", infraRows)}
        </div>
      );
    }
  };
}

export default SkillDetail;
