import { Typography, IconButton } from "@material-ui/core";
import React, { Component, Fragment } from "react";
import "./Profile.css";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { library } from "@fortawesome/fontawesome-svg-core";
import { faEnvelope, faKey } from "@fortawesome/free-solid-svg-icons";
import {
  faTwitter,
  faGithub,
  faFacebook,
  faInstagram
} from "@fortawesome/free-brands-svg-icons";
library.add(faEnvelope, faKey, faGithub);
const isMobile = window.innerWidth <= 500;

export class Profile extends Component {
  goToPage = (url: string) => {
    window.location.href = url;
  };
  render = () => {
    if (isMobile) {
      return (
        <Fragment>
          <div className="name-area">
            <Typography className="my-name-mobile">Yuta Sakou</Typography>
            <Typography className="my-nickname-mobile">@RinGoku</Typography>
          </div>
          <div className="icon-area">
            <IconButton
              onClick={() => this.goToPage("https://github.com/RinGoku")}
            >
              <FontAwesomeIcon className="portfolio-icon" icon={faGithub} />
            </IconButton>
            <IconButton
              onClick={() => this.goToPage("https://twitter.com/RinGoku98")}
            >
              <FontAwesomeIcon className="portfolio-icon" icon={faTwitter} />
            </IconButton>
            <IconButton
              onClick={() =>
                this.goToPage("https://www.facebook.com/yuta.sako")
              }
            >
              <FontAwesomeIcon className="portfolio-icon" icon={faFacebook} />
            </IconButton>
            <IconButton
              onClick={() =>
                this.goToPage("https://www.instagram.com/yuyu80105/")
              }
            >
              <FontAwesomeIcon className="portfolio-icon" icon={faInstagram} />
            </IconButton>
          </div>
        </Fragment>
      );
    } else {
      return (
        <Fragment>
          <div className="name-area">
            <Typography className="my-name">Yuta</Typography>
            <Typography className="my-name">Sakou</Typography>
            <Typography className="my-nickname">@RinGoku</Typography>
          </div>
          <div className="icon-area">
            <IconButton
              onClick={() => this.goToPage("https://github.com/RinGoku")}
            >
              <FontAwesomeIcon className="portfolio-icon" icon={faGithub} />
            </IconButton>
            <IconButton
              onClick={() => this.goToPage("https://twitter.com/RinGoku98")}
            >
              <FontAwesomeIcon className="portfolio-icon" icon={faTwitter} />
            </IconButton>
            <IconButton
              onClick={() =>
                this.goToPage("https://www.facebook.com/yuta.sako")
              }
            >
              <FontAwesomeIcon className="portfolio-icon" icon={faFacebook} />
            </IconButton>
            <IconButton
              onClick={() =>
                this.goToPage("https://www.instagram.com/yuyu80105/")
              }
            >
              <FontAwesomeIcon className="portfolio-icon" icon={faInstagram} />
            </IconButton>
          </div>
        </Fragment>
      );
    }
  };
}

export default Profile;
